<template>
  <div>
    <!-- 面包屑导航区 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品分类</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片内容区 -->
    <el-card>
      <el-button type="primary" @click="showAddCateDialog"
        >添加商品分类</el-button
      >
      <!-- 表格数据区域 -->
      <el-table
        :data="cateList"
        row-key="cat_id"
        border
        :header-cell-style="{ textAlign: 'center', color: '#000' }"
        :tree-props="{ children: 'children' }"
      >
        <el-table-column prop="cat_name" label="分类名称"> </el-table-column>
        <el-table-column
          prop="cat_deleted"
          label="是否有效"
          align="center"
          sortable
        >
          <template slot-scope="scope">
            <i
              class="el-icon-success"
              :style="{ color: 'lightgreen' }"
              v-if="scope.row.cat_deleted === false"
            ></i>
            <i class="el-icon-error" :style="{ color: 'red' }" v-else></i>
          </template>
        </el-table-column>
        <el-table-column prop="cat_level" label="等级" align="center">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.cat_level === 0" size="medium">一级</el-tag>
            <el-tag
              type="success"
              v-else-if="scope.row.cat_level === 1"
              size="medium"
              >二级
            </el-tag>
            <el-tag type="warning" size="medium" v-else>三级</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button
              type="primary"
              size="mini"
              @click="showEditDialog(scope.row.cat_id)"
              >编辑</el-button
            >
            <el-button
              type="danger"
              size="mini"
              @click="removeCate(scope.row.cat_id)"
              >删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页区域 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInf.pagenum"
        :page-sizes="[5, 10, 20, 30]"
        :page-size="queryInf.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 添加分类对话框 -->
    <el-dialog
      title="添加商品分类"
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="addCateClosed"
    >
      <el-form
        :model="cateForm"
        :rules="cateFormRules"
        ref="cateFormRef"
        label-width="100px"
      >
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model.trim="cateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="父级分类">
          <el-cascader
            v-model="cascaderSelectedKeys"
            :options="parentCateList"
            :props="cascaderProps"
            @change="cascaderChange"
            :style="{ width: '100%' }"
            clearable
          ></el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCate">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 编辑分类对话框 -->
    <el-dialog
      title="修改商品分类"
      :visible.sync="editCateDialogVisible"
      width="50%"
      @close="editCateClosed"
    >
      <el-form
        :model="editCateForm"
        :rules="editCateFormRules"
        ref="editCateFormRef"
        label-width="100px"
      >
        <el-form-item label="分类名称" prop="cat_name">
          <el-input v-model.trim="editCateForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editCateDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editCate(cateId)">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created() {
    this.getCateList()
  },
  data() {
    return {
      // 商品分类列表查询参数
      queryInf: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 商品分类列表
      cateList: [],
      // 商品分类项目总数
      total: 0,
      // 控制添加分类对话框出现
      addCateDialogVisible: false,
      editCateDialogVisible: false,
      // 添加分类表单
      cateForm: {
        cat_name: '',
        cat_level: 0,
        cat_pid: 0
      },
      editCateForm: {
        cat_name: ''
      },
      // 添加分类表单验证规则
      cateFormRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      },
      editCateFormRules: {
        cat_name: [
          { required: true, message: '请输入分类名称', trigger: 'blur' }
        ]
      },
      // 父级分类列表
      parentCateList: [],
      // 级联选择器中被选中的项
      cascaderSelectedKeys: [],
      cascaderProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children',
        checkStrictly: true
      },
      // 当前选中编辑的分类对应的cat_id
      cateId: null
    }
  },
  methods: {
    // 获取商品分类数据列表
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInf
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取商品分类列表失败')
      }
      this.cateList = res.data.result
      this.total = res.data.total
    },
    // 获取当前页码
    handleSizeChange(val) {
      this.queryInf.pagesize = val
      this.getCateList()
    },
    // 获取当前页面大小
    handleCurrentChange(val) {
      this.queryInf.pagenum = val
      this.getCateList()
    },
    // 显示添加分类
    async showAddCateDialog() {
      const { data: res } = await this.$http.get('categories', {
        params: {
          type: 2
        }
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取父级分类列表失败')
      }
      this.parentCateList = res.data
      this.addCateDialogVisible = true
    },
    // 监听级联选择器的变化
    cascaderChange() {
      const length = this.cascaderSelectedKeys.length
      if (length > 0) {
        this.cateForm.cat_pid = this.cascaderSelectedKeys[length - 1]
        this.cateForm.cat_level = length
      } else {
        this.cateForm.cat_pid = 0
        this.cateForm.cat_level = 0
      }
    },
    // 添加分类
    addCate() {
      this.$refs.cateFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            'categories',
            this.cateForm
          )
          if (res.meta.status !== 201) {
            return this.$message.error('添加商品分类失败')
          }
          this.$message.success('添加商品分类成功')
          this.getCateList()
          this.addCateDialogVisible = false
        }
      })
    },
    // 添加分类对话框关闭后，重置一下参数
    addCateClosed() {
      this.$refs.cateFormRef.resetFields()
      this.cascaderSelectedKeys = []
      this.cateForm.cat_pid = 0
      this.cateForm.cat_level = 0
    },
    // 编辑商品分类
    async showEditDialog(cateId) {
      this.cateId = cateId
      this.editCateDialogVisible = true
      const { data: res } = await this.$http.get(`categories/${cateId}`)
      if (res.meta.status !== 200) {
        return this.$message.error('请求商品分类信息失败')
      }
      this.editCateForm = res.data
    },
    // 编辑对话框关闭后，重置
    editCateClosed() {
      this.$refs.editCateFormRef.resetFields()
    },
    // 提交修改
    editCate(cateId) {
      this.$refs.editCateFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.put(`categories/${cateId}`, {
            cat_name: this.editCateForm.cat_name
          })
          if (res.meta.status !== 200) {
            return this.$message.error('修改商品分类名称失败')
          }
          this.$message.success('修改商品分类名称成功')
          this.getCateList()
          this.editCateDialogVisible = false
        }
      })
    },
    // 删除商品分类
    async removeCate(cateId) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除该分类, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { data: res } = await this.$http.delete(`categories/${cateId}`)
      if (res.meta.status !== 200) {
        return this.$message.error('删除商品分类失败')
      }
      this.$message.success('删除商品分类成功')
      this.getCateList()
    }
  }
}
</script>

<style lang="less" scoped></style>
